import React, { useEffect }  from "react";
import { ExportTextToFile } from "../common/fileHelper";
function ConfigContainer()
{
    useEffect(() => {
        ExportTextToFile("Test");
      }, []);
    return (
        <>
            <div>Thiêt lập hệ thống</div>
            <div>
                <div>Khung giờ:</div> 
                <div><input type="time" /> ~ <input type="time" /></div>
            </div>
            <div>
                <div>Số người:</div> 
                <div><input type="number" /></div>
            </div>
            <div>
                <div>Số phương tiện:</div> 
                <div><input type="number" /></div>
            </div>
            <div>
                <div>Thời gian giữa các lần cảnh báo:</div> 
                <div><input type="number" min={0} defaultValue={10} />(s)</div>
            </div>
            <div>
                <div>Tăng mức cảnh báo sau:</div> 
                <div><input type="number" min={0} defaultValue={5} />(cảnh báo)</div>
            </div>
    </>
    );
}

export default ConfigContainer;